import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Timeline from '@material-ui/lab/Timeline';
import TimelineItem from '@material-ui/lab/TimelineItem';
import TimelineSeparator from '@material-ui/lab/TimelineSeparator';
import TimelineConnector from '@material-ui/lab/TimelineConnector';
import TimelineContent from '@material-ui/lab/TimelineContent';
import TimelineOppositeContent from '@material-ui/lab/TimelineOppositeContent';
import TimelineDot from '@material-ui/lab/TimelineDot';
import FastfoodIcon from '@material-ui/icons/Fastfood';
import LaptopMacIcon from '@material-ui/icons/LaptopMac';
import HotelIcon from '@material-ui/icons/Hotel';
import RepeatIcon from '@material-ui/icons/Repeat';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Avatar from '@material-ui/core/Avatar';

import begIcon from '../assets/ejesProv/begicon.jpg';
import billetera from '../assets/ejesProv/bille.png';
import obras from '../assets/ejes/obr.png';
import vacuna from '../assets/ejesProv/vacSF.png';
import incluir from '../assets/ejesProv/incluir.jpg';
import hacemosSF from '../assets/ejesProv/hacemossf.png';


export default function CustomizedTimeline() {
  const classes = useStyles();

  return (
    <Timeline  align="alternate">
         <TimelineItem>   
            
        <TimelineSeparator>
          <TimelineDot >
           <Avatar src={hacemosSF} style={{height: 140, width: 140}} />
          </TimelineDot>
        <TimelineConnector />
        </TimelineSeparator>
        <TimelineContent>
         
        </TimelineContent>
        
      </TimelineItem>

        {provBtn.map((item, i) => (
            <TimelineItem>   
                 <TimelineOppositeContent>
          <Typography variant="body2" color="textSecondary">
            Inscribite en este Link
          </Typography>
        </TimelineOppositeContent>
        <TimelineSeparator>
          <TimelineDot  >
           <Avatar src={item.img} className={classes.timelineAvatar} />
          </TimelineDot>
        <TimelineConnector />
        </TimelineSeparator>
        <TimelineContent>
         {item.text &&  <CardTimeLine text={item.text} />}
        </TimelineContent>
      </TimelineItem>
        ))}
      
     
    </Timeline>
  );
}

const CardTimeLine = ({text}) => {
const classes = useStyles();

    return <Paper elevation={2} className={classes.paper}>
            <Typography variant="h6" style={{fontSize: 16}}>
              Boleto Educativo Gratuito
            </Typography>
            <Typography style={{fontSize: 14}} >
              {text} 
            </Typography>
          </Paper>
}

const provBtn = [
   // {id: 'hacemos', img: hacemosSF, name: 'Hacemos Santa Fe'},
	{id: 'beg', img: begIcon, name: 'Boleto Educativo Gratuito', text: 'dadadada dadadada dadadada dadadada dadadada dadadada dadadada dadadada dadadada dadadada'},
	{id: 'billetera', img: billetera, name: 'billetera Santa Fe', text: 'dadadada dadadada dadadada dadadada dadadada dadadada dadadada dadadada dadadada dadadada'},
	{id: 'incluir', img: incluir, name: 'Plan Incluir', text: 'dadadada dadadada dadadada dadadada dadadada dadadada dadadada dadadada dadadada dadadada'},
	{id: 'vacuna', img: vacuna, name: 'Plan de Vacunacion', text: 'dadadada dadadada dadadada dadadada dadadada dadadada dadadada dadadada dadadada dadadada'},
	{id: 'obras', img: obras, name: 'Obras Publicas', text: 'dadadada dadadada dadadada dadadada dadadada dadadada dadadada dadadada dadadada dadadada'}
]

const useStyles = makeStyles((theme) => ({
  paper: {
    padding: '8px 16px',
  },
  secondaryTail: {
    backgroundColor: theme.palette.secondary.main,
  },
  timelineAvatar: {
      height: 100,
      width: 100
  }
}));