import './App.css';
import Home from './pages/index'
import { ThemeProvider, createMuiTheme } from '@material-ui/core/styles';
import { Alert } from '@material-ui/lab';


const theme = createMuiTheme({
	typography: {
	  fontFamily: [
		'Sora',
		'sans-serif' 
	  ].join(','),
	},});

function App() {


    window.onpopstate = ()=> {
      return
	 
    }

	

	return (
		<>
			<ThemeProvider theme={theme}>
    <Home/>
	</ThemeProvider>
		</>
	);
}

export default App;
